
.loading-box{width:90%;margin:0 auto;height:1rem;position:relative;overflow:hidden;border-radius:0.5rem;
}
.loading-bar{width:100%;position:relative;height:100%;background:#67D22D;left:-100%;-webkit-transition:left 1s ease;transition:left 1s ease
}



