.modalBox[data-v-6e454ca0]{position:fixed;width:100%;height:100%;top:0;left:0;z-index:102;
}
.modal[data-v-6e454ca0]{width:13rem;margin:-3rem auto 0;top:50%;background:#fff;position:relative;border-radius:0.4rem;overflow:hidden;-webkit-transition:-webkit-transform 0.4s ease;transition:-webkit-transform 0.4s ease;transition:transform 0.4s ease;transition:transform 0.4s ease, -webkit-transform 0.4s ease;
}
.modal .modalHeader[data-v-6e454ca0],.modal .modalContent[data-v-6e454ca0],.modal .modalFooter[data-v-6e454ca0]{text-align:center;
}
.modal .modalHeader[data-v-6e454ca0]{font-size:0.9rem;padding:1em 0.5rem 0.2rem 0.5rem;
}
.modal .modalContent[data-v-6e454ca0]{font-size:0.8rem;padding:0 0.5rem 1rem 0.5rem;
}
.modal .modalFooter[data-v-6e454ca0]{border-top:1px solid #e2e2e2;padding:0;line-height:1.8rem;
}
.modal .modalFooter .modalBtn[data-v-6e454ca0]{float:left;width:50%;height:100%;font-size:0.8rem;
}
.modal .modalFooter .modalBtn[data-v-6e454ca0]:first-child{border-right:1px solid #e2e2e2;
}
.modalBtnBgGray[data-v-6e454ca0]{background:#e2e2e2;
}

