/*modal*/
.modalBox {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-perspective: 500px;
          perspective: 500px;
  z-index: 100;
}
.modalBg {
  background: #000;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 99;
}
.modal {
  width: 50%;
  max-width: 20rem;
  margin: -3rem auto 0;
  top: 50%;
  background: #fff;
  position: relative;
  border-radius: 0.4rem;
  overflow: hidden;
}
.modal .modalHeader, .modal .modalContent, .modal .modalFooter {
  text-align: center;
}
.modal .modalHeader {
  font-size: 0.7rem;
  padding: 0.8em 0.5rem 0.2rem 0.5rem;
}
.modal .modalContent {
  font-size: 0.65rem;
  padding: 0 0.5rem 0.8rem 0.5rem;
}
.modal .modalFooter {
  border-top: 1px solid #e2e2e2;
  padding: 0;
  line-height: 1.8rem;
}
.modal .modalFooter .modalBtn {
  float: left;
  width: 50%;
  height: 100%;
  font-size: 0.7rem;
  color: #0577FF;
}
.modal .modalFooter .modalBtn:first-child {
  border-right: 1px solid #e2e2e2;
}
.modalBtnBgGray {
  background: #e2e2e2;
}

